<script>
export default {
    name: 'CartSection',
    props: {
        cart: {
            type: Array,
            required: true
        }
    },
    computed: {
        totalPrice() {
            return this.cart.reduce((total, item) => total + this.calculatePrice(item), 0);
        }
    },
    methods: {
        calculatePrice(item) {
            return item.basePrice;
        },
        removeFromCart(item) {
            this.$emit('remove-from-cart', item);
        },
        checkout() {
            this.$emit('checkout');
        }
    }
};
</script>

